import { Module } from 'vuex';
import AuthMethods from '@/store/auth/methods/auth.methods';
import { authHttpRepository }from '@/modules/auth/repositories/auth.repository';
import { AUTH_EMPTY_STATE } from './auth.state';
import { UserStateInterface } from './interfaces/auth.state.interface';
import { User, UserAuthResponse, UserLogin} from '@/modules/auth/interfaces/user.interface';
import store from '@/store';
import { USER_EMPTY_STATE } from '@/modules/auth/emptyStates/user.emptyState';
import {getCountry} from 'country-list-spanish';
import { finishSession } from '@/modules/auth/functions/functions';
import { spanishDateToEnglishDate } from '@/utils/date-functions';

const auth: Module<UserStateInterface, any> = {
    namespaced: true,
    state: AUTH_EMPTY_STATE,
    getters: {
        [AuthMethods.getters.GET_USER_DATA](state): User {
            return state.user;
        },
        [AuthMethods.getters.GET_AUTHENTICATION](state): boolean {
            return state.isAuthenticated;
        },
        [AuthMethods.getters.GET_REGISTER_USER_DATA](state): User {
            return state.userSignUp;
        },
        [AuthMethods.getters.GET_COUNTRY_PHONE_CODE](state): string {
            return state.user.main_phone_code;
        },
        [AuthMethods.getters.GET_SELECTED_DOC_TYPE](state): number {
            return state.selectedTypeDocType;
        },
        [AuthMethods.getters.GET_SELECTED_COUNTRY_DOC](state): string {
            return state.selectedCountryIdDocType;
        },
        [AuthMethods.getters.GET_ATC_PHONE](state): string {
            return state.atcPhone;
        },
        [AuthMethods.getters.GET_LEVELS_QUESTIONS](state): Array<any>{
            return state.levelQuestions
        }
    },
    mutations: {
        [AuthMethods.mutations.SET_USER_DATA](state, user: User): void {
            state.user = user;
        },
        [AuthMethods.mutations.SET_REGISTER_USER_DATA](state, user: User): void {
            state.userSignUp = user;
        },
        [AuthMethods.mutations.SET_COUNTRY_PHONE_CODE](state, code: string): void {
            state.userSignUp.main_phone_code = code;
        },
        [AuthMethods.mutations.SET_AUTH](state, isAuthenticated : boolean): void {
            state.isAuthenticated = isAuthenticated;
        },
        [AuthMethods.mutations.SET_ATC_PHONE](state, atcPhone : string): void {
            state.atcPhone = atcPhone;
        },
        [AuthMethods.mutations.SET_LEVELS_QUESTIONS](state, questions : Array<any>): void {
            state.levelQuestions = questions;
        },
        [AuthMethods.mutations.SET_SELECTED_COUNTRY_DOC](state, countryDocId : string): void {
            state.selectedCountryIdDocType = countryDocId;
        },
        [AuthMethods.mutations.SET_SELECTED_DOC_TYPE](state, docTypeId : number): void {
            state.selectedTypeDocType = docTypeId;
        },
        [AuthMethods.mutations.CLEAR_AUTH_STATE](state, cleanedState : UserStateInterface): void {
            state = cleanedState;
        }
    },
    actions: {
        [AuthMethods.actions.SAVE_SELECTED_COUNTRY_ID_DOC]({ commit },countryDocId: string): void {
            try {
              commit(AuthMethods.mutations.SET_SELECTED_COUNTRY_DOC,countryDocId)
            } catch (e) {
            }
        },
        [AuthMethods.actions.SAVE_SELECTED_DOC_QUESTION_NUMBER]({ commit },docTypeId: number): void {
            try {
              commit(AuthMethods.mutations.SET_SELECTED_DOC_TYPE,docTypeId)
            } catch (e) {
            }
        },
        async [AuthMethods.actions.LOGIN]({ commit }, user: UserLogin): Promise<{errorMessage?: string, successMessage?: string, redirect?: string}> {
            let errorMessage = 'Correo o clave invlida. Recuerde que con 3 intentos fallidos se bloquear su cuenta'
            try {
                let userState: UserAuthResponse  = await authHttpRepository.login(user);
                if (userState.captchaSuccess === false)
                    return {errorMessage: userState.msg}
                else {
                    if (userState.userExists){
                        commit(AuthMethods.mutations.SET_ATC_PHONE, userState.atcPhone);
                        if (userState.loginAttempts >= 1 && userState.loginAttempts <=3) return {errorMessage: errorMessage}
                    }
                    if (userState.isAuthenticated){
                        commit(AuthMethods.mutations.SET_USER_DATA, userState.user);
                        commit(AuthMethods.mutations.SET_AUTH, true);
                        return {successMessage: 'Inicio exitoso. Bienvenido a Criptoremesa'};
                    }else{
                        return {errorMessage: errorMessage};
                    }  
                }
            } catch (e) {
                if (e.response.data.user_blocked === true) { 
                    commit(AuthMethods.mutations.SET_ATC_PHONE, "+5638237283");
                    return {redirect: 'UserBlocked'}
                }
                if (e.response.data.id_verif_level === 0 && e.response.data.verif_level_apb === null){
                    return {redirect: 'LevelCeroVerification'}
                }
                return {errorMessage: errorMessage};
            }
        },
        async [AuthMethods.actions.LOGOUT]({ commit }): Promise<boolean> {
            try {
               let response : {message: string} = await authHttpRepository.logout();
               if (response.message === 'Logged out succesfully'){
                    finishSession()
                    return true
                }
               return false
            } catch (e) {
               return false
            }
        },
        async [AuthMethods.actions.CREATE_CLIENT]({ commit }, user: Partial<User>): Promise<{errorMessage: string} | boolean> {
            try {
                 let phone_code = user.main_phone_code
                 let register: UserAuthResponse = await authHttpRepository.createClient(user);
                 if (register.user && register.captchaSuccess){
                    commit(AuthMethods.mutations.SET_REGISTER_USER_DATA, USER_EMPTY_STATE);
                    commit(AuthMethods.mutations.SET_COUNTRY_PHONE_CODE,phone_code)
                    return true
                 }
                 else return {errorMessage: 'Ha ocurrido un error al crear el usuario'}
            } catch (e) {
                // if (e.response.data.error || !e.response.captchaSuccess)
                //     return {errorMessage: e.response.data.msg}
                if (e.response.data.msg === "Email already exists")
                    return {errorMessage: 'El correo ingresado ya est en uso. Intente de nuevo'}
                else return {errorMessage: 'Ha ocurrido un error al crear el usuario'};
            }
        },
        async [AuthMethods.actions.SEND_EMAIL]({ commit }, data: {email_user: string, type: string}): Promise<{error: boolean, msg: string}> {
            try {
                 let response: any = undefined;
                 if (data.type === 'recoverPassword') response = await authHttpRepository.sendRecoverPasswordEmail({email_user: data.email_user});
                 else if (data.type === 'register') response = await authHttpRepository.sendRegisterVerificationEmail({email_user: data.email_user})
                 if (response.msg === "Code generated" && response.mailResp === "Email succesfully sent") 
                    return {error: false, msg:"Se ha enviado el correo exitosamente"}
                else 
                    return {error: true,msg: "Ha ocurrido un error enviando el correo"};
            } catch (e) {
                 if (e.response.data.msg  === "The email does not exist") return {error: true, msg: "El correo ingresado es invlido" }
                 else return {error: true,msg: "Ha ocurrido un error enviando el correo"};
            }
        },
        async [AuthMethods.actions.CHANGE_PASSWORD]({ commit }, data: {email_user: string, new_password: string,last_password: string}): Promise<{error: boolean, msg: string}> {
            try {
                let response: any = await authHttpRepository.changePassword(data);
                if (response.msg === "Password succesfully changed") return {error: false, msg: 'Contrasea actualizada exitosamente'}
                return {error: true, msg: 'Ha ocurrido un error actualizando la contrasea'}
            } catch (e) {
                if (e.response.data.msg === "The password cannot be equal to last 3")
                    return {error: true, msg: "La nueva contrasea debe ser distinta a las ltimas tres"}
                else if (e.response.data.msg === "Incorrect last password") return {error: true, msg: 'Contrasea actual incorrecta, intente de nuevo'}
                return  {error: true, msg: 'Ha ocurrido un error actualizando la contrasea'}
            }
        },
        async [AuthMethods.actions.VERIF_CLIENT]({ commit }, email: string): Promise<boolean> {
            try {
                 //user.id_nationality_country = 1;
                 let userLogged: any = await authHttpRepository.verifClient(email);
                 if (userLogged.message === "User level cero approved succesfully"){
                    return true
                 }
                 else return false
            } catch (e) {
                return false;
            }
        },
        async [AuthMethods.actions.VERIFY_LEVEL_ONE]({ commit }, data: {question: number, country: string, data: any}): Promise<boolean> {
            try {
                let docFile = undefined;
                let selfieFile = undefined;
                if (data.data.doc.value.name){ docFile = new File([data.data.doc.value], 'req-level-1-doc.png',{type: data.data.doc.value.type});}
                else{
                    let blob1 = new Blob([new Uint8Array(data.data.doc.value.data)])
                    docFile = new File([blob1], 'req-level-1-doc.png',{type:'image/png'})
                }
                if (data.data.selfie.value.name){ selfieFile = new File([data.data.selfie.value], 'req-level-1-selfie.png',{type: data.data.selfie.value.type});}
                else{
                    let blob1 = new Blob([new Uint8Array(data.data.selfie.value.data)])
                    selfieFile = new File([blob1], 'req-level-1-selfie.png',{type:'image/png'})
                }
                let mainPlatf = null
                if (data.data.main_sn_platf.value !== 'Ninguna') mainPlatf = data.data.main_sn_platf.value
                let englishDate = spanishDateToEnglishDate(data.data.date_birth.value)
                let bodyFormData = new FormData();
                bodyFormData.append('date_birth', englishDate);
                bodyFormData.append('state_name', data.data.state_name.value);
                bodyFormData.append('resid_city', data.data.resid_city.value);
                if (data.data.pol_exp_per.value !== null && data.data.pol_exp_per.value !== undefined) 
                    bodyFormData.append('pol_exp_per', data.data.pol_exp_per.value.toString());
                else bodyFormData.append('pol_exp_per', 'false');
                bodyFormData.append('email_user', data.data.email_user.value);
                if (data.question === 1)
                    bodyFormData.append('id_ident_doc_type', data.data.id_ident_doc_type.value.toString());
                else 
                    bodyFormData.append('id_country',data.country);
                bodyFormData.append('ident_doc_number', data.data.ident_doc_number.value);
                bodyFormData.append('occupation', data.data.occupation.value);
                bodyFormData.append('doc', docFile);
                bodyFormData.append('selfie', selfieFile);
                bodyFormData.append('main_sn_platf', mainPlatf);
                bodyFormData.append('user_main_sn_platf', data.data.user_main_sn_platf.value);
                let response: {message?: string} = undefined;
                if (data.question === 1)
                    response = await authHttpRepository.verifyLevelOneFirstQuestion(bodyFormData);
                else if (data.question === 2)
                    response = await authHttpRepository.verifyLevelOneSecondQuestion(bodyFormData);
                else if (data.question === 3)
                    response = await authHttpRepository.verifyLevelOneThirdQuestion(bodyFormData);
                if (response.message) return true
                return false
            } catch (e) {
                return false;
            }
        },
        async [AuthMethods.actions.FETCH_LEVELS_QUESTIONS]({ commit }): Promise<boolean> {
            try {
                let response = await authHttpRepository.fetchLevelQuestions();
                if (response.length > 0){
                    commit(AuthMethods.mutations.SET_LEVELS_QUESTIONS,response);
                    return true;
                }
                return false
            } catch (e) {
                return false;
            }
        },
        async [AuthMethods.actions.PROTECTED_ROUTE]({ commit }): Promise<boolean> {
            try {
                await authHttpRepository.protectedRoute();
                return false
            } catch (e) {
                return false;
            }
        },
    },
    
};

export default auth;
