const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  to.matched.some((route) => {
    /* If the route requires Authentication */
        if (route.meta.requiresAuth) {
            if (store.state.auth.isAuthenticated) {
              requiresPreceding(route,from,next);
            } else {
              next({ name: 'Login' });
            }
        } else {
          /* Routes that user can't go if isAuthenticated */
          if (store.state.auth.isAuthenticated && route.meta.authBlocked) next({name: 'Dashboard'})
          else requiresPreceding(route,from,next);
        }
  });
});

/* If the route needs to be preceded by another route */
function requiresPreceding(to,from,next){
  if (to.meta.requiresPreceding){
    if (from.meta.nextRoute === to.path)
      next()
    else 
      next({ path: to.meta.firstStep})
  }
  else next()
}

export default router;
