import { AxiosRepository } from '@/http/axios.repository';
// import { Route } from '@/modules/routes/interfaces/routes.interface';
import { User } from '@/modules/auth/interfaces/user.interface';
// import { Test } from '@/modules/test/interfaces/test.interface';

const LOGIN_RESOURCE = 'login';
const LOGOUT_RESOURCE = 'logout';
const CREATE_RESOURCE = 'users/create';
const CREATE_CLIENT_RESOURCE = 'users/createNewClient';
const GET_CLIENT_RESOURCE = 'users/getClientById';
const GET_EMPLOYEE_RESOURCE = 'users/getEmployeeById';
const GET_EMPLOYEE_PHONES_RESOURCE = 'users/getEmployeePhonesById';
const GET_CLIENT_PHONES_RESOURCE = 'users/getClientPhonesById';
const EDIT_USER_RESOURCE = 'users/updateClient'
const BLOCK_CLIENT_RESOURCE = 'users/blockClient'
const BLOCK_EMPLOYEE_RESOURCE = 'users/blockEmployee'
const UNBLOCK_CLIENT_RESOURCE = 'users/unblockClient'
const UNBLOCK_EMPLOYEE_RESOURCE = 'users/unblockEmployee'
const EDIT_EMPLOYEE_RESOURCE = 'users/updateEmployee'
const VERIF_CLIENT_RESOURCE = 'users/approveLevelCero';
const VERIFY_LEVEL_ONE = 'users/requestLevelOne1stQ'
const VERIFY_LEVEL_ONE_2 = 'users/requestLevelOne2ndQ'
const VERIFY_LEVEL_ONE_3 = 'users/requestLevelOne3rdQ'
const SEND_RECOVER_PASS_EMAIL_RESOURCE = 'users/forgotPassword'
const CHANGE_PASSWORD_RESOURCE = 'users/newPassword'
const SEND_SIGNUP_VERIFICATION_RESOURCE = 'users/sendVerificationCodeByEmail'
const LEVEL_QUESTIONS_RESOURCE = 'users/getLevelQuestions'

export class AuthRepository extends AxiosRepository {
    
    async login(data: any): Promise<any> { 
        return super.post(data,LOGIN_RESOURCE);
    }

    async logout(): Promise<any> { 
        return super.getAll(LOGOUT_RESOURCE);
    }

    // async getMainMenuRoutes(id : string): Promise<Route[]>{
    //     return super.get(`'`+id+`'`,ROUTES_MAINMENU_RESOURCE);
    // }

    async create(data: any): Promise<any> { 
        return super.post(data,CREATE_RESOURCE);
    }

    async createClient(data: any): Promise<any> { 
        return super.post(data,CREATE_CLIENT_RESOURCE);
    }

    async getClientById(id : string): Promise<User>{
        return super.get(`'`+id+`'`,GET_CLIENT_RESOURCE);    
    }

    async verifClient(email : string): Promise<any>{
        return super.get(`'`+email+`'`,VERIF_CLIENT_RESOURCE);    
    }

    async getEmployeeById(id : string): Promise<User>{
        return super.get(`'`+id+`'`,GET_EMPLOYEE_RESOURCE);    
    }

    async getEmployeePhonesById(id : string): Promise<User>{
        return super.get(`'`+id+`'`,GET_EMPLOYEE_PHONES_RESOURCE);    
    }

    async getClientPhonesById(id : string): Promise<User>{
        return super.get(`'`+id+`'`,GET_CLIENT_PHONES_RESOURCE);    
    }

    async editClient(client : any): Promise<any>{
        return super.put(client,EDIT_USER_RESOURCE);
    }

    async editEmployee(employee : any): Promise<any>{
        return super.put(employee,EDIT_EMPLOYEE_RESOURCE);
    }

    async blockClient(id : number): Promise<any>{
        return super.delete(id,BLOCK_CLIENT_RESOURCE);
    }

    async blockEmployee(id : number): Promise<any>{
        return super.delete(id,BLOCK_EMPLOYEE_RESOURCE);
    }

    async unblockClient(id : number): Promise<any>{
        return super.delete(id,UNBLOCK_CLIENT_RESOURCE);
    }

    async unblockEmployee(id : number): Promise<any>{
        return super.delete(id,UNBLOCK_EMPLOYEE_RESOURCE);
    }
    async verifyLevelOneFirstQuestion(data: any ): Promise<any>{
        return super.postData(data,VERIFY_LEVEL_ONE);
    }
    async verifyLevelOneSecondQuestion(data: any ): Promise<any>{
        return super.postData(data,VERIFY_LEVEL_ONE_2);
    }
    async verifyLevelOneThirdQuestion(data: any ): Promise<any>{
        return super.postData(data,VERIFY_LEVEL_ONE_3);
    }

    async sendRecoverPasswordEmail(data: {email_user: string}): Promise<any>{
        return super.post(data,SEND_RECOVER_PASS_EMAIL_RESOURCE)
    }

    async sendRegisterVerificationEmail(data: {email_user: string}): Promise<any>{
        return super.post(data,SEND_SIGNUP_VERIFICATION_RESOURCE)
    }

    async fetchLevelQuestions(): Promise<any>{
        return super.getAll(LEVEL_QUESTIONS_RESOURCE)
    }

    async changePassword(data: {email_user: string, new_password: string}): Promise<any>{
        return super.post(data,CHANGE_PASSWORD_RESOURCE)
    }

    async protectedRoute(){
        return super.getAll('protected-route')
    }
}

export const authHttpRepository = new AuthRepository();